    font-family: Arial, Helvetica, sans-serif;
}

.wallet-content-text h1{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  padding-top: 11px;
    margin-bottom: 1rem;
    min-height: 148px;
    border-bottom: 1px solid #faf8f18f;
}

.spec2 h5{
    font-size: 2rem !important;
    color: rgb(253,86,42)
}

.wallet-content-text p{
  font-size: 1rem !important;
}


       
          

